<template>
    <div class="pieMain"></div>
</template>

<script>
// 直接导入echarts库
import * as echarts from 'echarts';

export default {
    name: 'Chart6',
    props: {
        data: {
            type: Object,
            default: () => ({
                title: '默认标题',
                data: [],
                data1: []
            })
        }
    },
    data() {
        return {
            myChart: null
        }
    },
    methods: {
        truncateText(text, length = 10) {
            if (!text) return '';
            return text.length > length ? text.substring(0, length) : text;
        },
        
        setChart() {
            if (!this.data || !this.data.data) {
                console.warn('chart6: 数据未准备好')
                return
            }

            // 确保数据中的名称不会过长
            const processedData = this.data.data.map(item => ({
                ...item,
                name: this.truncateText(item.name)
            }));
            
            const processedData1 = this.data.data1.map(item => ({
                ...item,
                name: this.truncateText(item.name)
            }));

            let option = {
                title: {
                    text: '【' + this.data.title + '】',
                    x: 'center',
                    top: 0,
                    textStyle: {
                        color: '#75deef',
                        fontSize: 12,
                        fontWeight: 'normal'
                    }
                },
                tooltip: {
                    trigger: 'item',
                    formatter: "{b} : {c} ({d}%)",
                    backgroundColor: '#11367a',
                    textStyle: {
                        color: '#6dd0e3',
                        fontSize: 10,
                    },
                },
                series: [
                    {
                        type: 'pie',
                        startAngle: 160,
                        radius: [0, '30%'],
                        label: {
                            show: true,
                            position: 'inside',
                            fontSize: 10,
                        },
                        center: ['50%', '60%'],
                        data: processedData,
                    },
                    {
                        type: 'pie',
                        startAngle: 160,
                        radius: ['52%', '72%'],
                        labelLine: {
                            lineStyle: {
                                color: '#444b62',
                            },
                            length: 5,
                            length2: 5,
                        },
                        emphasis: {
                            label: {
                                color: '#fff',
                                show: true,
                            },
                            itemStyle: {
                                shadowBlur: 10,
                                shadowOffsetX: 0,
                                shadowColor: 'rgba(0, 0, 0, 0.5)'
                            }
                        },
                        label: {
                            position: 'outside',
                            borderRadius: 4,
                            color: '#fff',
                            fontSize: 10,
                            padding: 0,
                            backgroundColor: '#183566',
                            formatter: function(params) {
                                return params.name + ': ' + params.value + ' (' + params.percent + '%)';
                            }
                        },
                        center: ['50%', '60%'],
                        data: processedData1
                    }
                ]
            };
            
            // 使用直接导入的echarts初始化图表
            if (!this.myChart) {
                this.myChart = echarts.init(this.$el);
            }
            this.myChart.clear();
            this.myChart.resize();
            this.myChart.setOption(option);
            
            // 修复dispatchAction错误
            if (this.myChart && processedData && processedData.length > 0) {
                let obj = {
                    type: 'highlight',
                    name: []
                };
                for (let i = 0; i < processedData.length - 1; i++) {
                    obj.name.push(processedData[i].name)
                }
                
                // 延迟执行，确保图表已经渲染
                setTimeout(() => {
                    if (this.myChart) {
                        this.myChart.dispatchAction(obj);
                    }
                }, 100)
                
                // 修改事件绑定方式
                this.myChart.on('mouseout', () => {
                    if (this.myChart) {
                        this.myChart.dispatchAction(obj);
                    }
                });
            }
        },
    },
    mounted() {
        this.$nextTick(() => {
            this.setChart();
        })
    },
    watch: {
        data: {
            handler() {
                this.$nextTick(() => {
                    this.setChart();
                });
            },
            deep: true
        }
    },
    beforeDestroy() {
        if (this.myChart) {
            this.myChart.dispose()
            this.myChart = null
        }
    }
}
</script>

<style lang="less" scoped>
.pieMain {
    width: 100%;
    height: 100%;
}
</style>